er:solid red;\n    display:flex;\n    align-items:center;\n    justify-content: flex-end;\n    padding-right: 5vw;\n    padding-top:0;\n    //border-radius:0px 0px 10px 10px;\n    background-color: rgb(0, 0, 0, 0.9);\n    z-index:100;\n    \n\n    .nav-item{\n        //controll each element in the nav bar\n        list-style:none;\n        margin-right:8vw;\n        font-size: 1.5vw;\n        \n\n        a{\n            //controll the element in the nav abr\n            text-decoration: none;\n            color:white;\n        }\n    }\n    .nav-item:first-child{\n        //only controll the logo \n        margin-right:auto;\n        margin-left:10vw;\n        padding-top:0.5vw;\n    }\n\n    .text:hover{\n        color:#ffcc33;\n    }\n\n    .text::after{\n        content: '';\n        display:block;\n        margin-top:2px;\n        width:2px;\n        height:2px;\n        background: #ffcc33;\n        transition:width 0.5s;     \n    }\n    .text:hover::after{\n        width:100%;\n        transition:width 0.5s;   \n    }\n}\n.grid-container{\n    display:grid;\n    grid-template-columns:1fr 1fr 1fr;\n    grid-template-rows: 40vw 5vw minmax(40vw, auto);\n    grid-template-areas:\n    \"banner banner banner\"\n    \"filter filter filter\"\n    \"mov mov mov\";\n\n    .grid-object-1{\n        grid-area: banner;\n        //border:solid white;\n        background-image: url(\"../image/portfolio/set2.jpg\");\n        background-size: cover;\n        background-repeat: no-repeat;\n        \n        display:flex;\n\n        .para1{\n            color:white;\n            //padding-top: ;\n            padding-left:5vw;\n            \n            .line1{\n                font-size: 2vw;\n                margin-top:17vw;\n            }\n            .line2{\n                font-size: 5vw;\n                font-weight: bolder;\n                padding-top:1vw;\n                color:#ffcc33;\n            }\n        }\n\n        .border{\n            width:2vw;\n            height:14vw;\n            margin-top:15vw;\n            margin-left: 4vw;\n            border-left:solid #ffcc33;\n            border-radius: repeat(5px,4);\n        }\n\n        .para2{\n            color:white;\n            font-size:2vw;\n            margin-top:12vw;\n            margin-left:2vw;\n            margin-right:2vw;\n            text-align: left;\n            \n        }\n    }\n\n    \n    \n\n\n\n    .grid-object-2{\n        grid-area: filter;\n        //border:solid green;\n\n        background-color: black;\n        display:flex;\n        align-items: center;\n        \n        #myInput{\n            width:30vw;\n            height:3vw;\n            margin-left:2vw;\n            font-size:2vw;\n            border:2px solid #ffcc33;\n            border-radius: 5px;\n        }\n        .fixed{\n            \n            color:#ffcc33;\n            font-size: 2vw;\n            margin-left:3vw;\n        }\n        .dropDown{\n            color:white;\n            position:relative;\n            display:inline-block;\n\n            .dropbtn{\n                margin-left:2vw;\n                width:12vw;\n                height:3vw;\n                border:2px solid #ffcc33;\n                border-radius: 5px;\n                cursor: pointer;\n            }\n\n            .dropDownList{\n                position:absolute;\n                z-index:10;\n                margin-left:2vw;\n                \n                \n            }\n        }\n        .button{\n            display: none;\n            background-color: #f6f6f6;\n            border: 1px solid #ddd;\n            z-index: 1;\n            color:black;\n            font-size: 2vw;\n            cursor:pointer;\n            height:3.5vw;\n            width:12vw;\n            \n            text-align:center;\n            padding-top: 0.5vw;\n       \n        }\n        .button-show{\n            display:block;\n        }\n        .button:hover{\n            background-color: #ddd;\n        }\n