in-bottom:2vw;\n        cursor:pointer;\n        border:none;\n        outline:none;\n        background:none;\n        color: #ffcc33;\n        font-size:2vw;\n        //border:solid purple;\n    }\n    .fixed{\n        color:#ffcc33;\n        font-weight: lighter;\n    }\n\n    .slideshow{\n        grid-area:pic;\n        //border:solid red;\n        position:relative;\n        display:flex;\n        justify-content: center;\n        align-items: center;\n        margin-left:2vw;\n        margin-right:2vw;\n        // margin-bottom: 7vw;\n\n        .prev{\n            left:0;\n        }\n        .next{\n            right: 0;\n        }\n\n        .prev, .next{\n            color: #ffcc33;\n            cursor: pointer;\n            position: absolute;\n            font-size: 3vw;\n            top: 40%;\n            transition:0.2s;\n            background-color: rgba(0,0,0,0.2);\n        }\n\n        .prev:hover, .next:hover {\n            background-color: rgba(0,0,0,0.8);\n          }\n          \n    }\n\n    .title{\n        grid-area:title;\n        //border:solid blue;\n\n        .title-content{\n            color:white;\n            font-weight: bolder;\n            font-size:3vw;\n            \n        }\n    }\n\n    .genre{\n        grid-area: genre;\n        //padding-top: 2vw;\n        //border:solid yellow;\n        .genre-content{\n            color:white;\n            font-size:2vw;\n        }\n    }\n\n    .description{\n        grid-area: description;\n        \n        //border:solid white;\n        .description-content{\n            color:white;\n            font-size:2vw;\n        }\n    }\n\n    .link{\n        grid-area:link;\n        //border:solid green;\n        display:flex;\n        justify-content: space-around;\n        \n\n        img{\n            width:5vw;\n        }\n\n        .youtube{\n            margin-left:2vw;\n        }\n\n        .bilibili{\n            margin-right:2vw;\n        }\n    }\n}\n\n.modal-active{\n    transform